import axios from 'axios'

export default{
    getRates,
    getMarketPrice,
    getConfirmedTransactions
}

async function getRates(coins){
    try{
        const res = await axios.get(`https://blockchain.info/tobtc?currency=USD&value=${coins}`);
        return res.data
    }catch(err){
        console.log('error in bitcoinService-getRates:',err);
    }
}

async function getMarketPrice(){
    try {
        const res = await axios.get('https://api.blockchain.info/charts/market-price?format=json&cors=true')
        return res.data
    }catch(err){
        console.log('error in bitcoinService-getMarketPrice:',err);        
    }
}

async function getConfirmedTransactions(){
    try {
        const res = await axios.get('https://api.blockchain.info/charts/n-transactions?format=json&cors=true')
        return res.data
    }catch(err){
        console.log('error in bitcoinService-getConfirmedTransactions:',err);        
    }
}