import React from 'react';
import ContactService from '../../services/ContactService';
import { Link } from 'react-router-dom'


class ContactEditPage extends React.Component {
    state = {
        contact: {
            name: '',
            email: '',
            phone: ''
        }
    }
    async componentDidMount() {
        if (this.props.match.params.id) {
            const contactFromDB = await ContactService.getContactById(this.props.match.params.id)

            const contact = {
                _id: contactFromDB._id,
                name: contactFromDB.name,
                email: contactFromDB.email,
                phone: contactFromDB.phone
            }

            this.setState({ contact })
        }
    }

    handleTxtChange = (event) => {
        const { value, name } = event.target;
        this.setState((state) => { return { contact: { ...state.contact, [name]: value } } })
    }

    submit = async () => {
        await ContactService.saveContact(this.state.contact);
        this.props.history.push('/contact')
    }

    onDelete = () =>{
        ContactService.deleteContact(this.props.match.params.id);

        this.props.history.push('/contact')
    }

    render() {
        const id = this.props.match.params.id;
        return (
            <div className="contact-edit">
                <h3>edit page</h3>
                <input type="text" value={this.state.contact.name} placeholder="name"
                    onChange={this.handleTxtChange} name="name"></input>
                <br />
                <input type="text" value={this.state.contact.email} placeholder="email"
                    onChange={this.handleTxtChange} name="email"></input>
                <br />
                <input type="text" value={this.state.contact.phone} placeholder="phone"
                    onChange={this.handleTxtChange} name="phone"></input>
                <br />
                <button onClick={this.submit}>Submit</button>
                <br/>

                {id && <Link to={`/contact/${id}`}>Back</Link>}
                {!id && <Link to={`/contact`}>Back</Link>}

                {id && <button onClick={this.onDelete}>Delete</button>}

            </div>
        )
    }
}

export default ContactEditPage;