import React from 'react';
import { withRouter } from "react-router-dom";

class ContactPreview extends React.Component{

    openDetails = (contactId)=>{
        console.log(contactId);
        
       this.props.history.push(`/contact/${contactId}`)
    }

    render(){
        return(
            <div className="contact-preview" onClick={()=>this.openDetails(this.props.contact._id)}>
                <img src = {`https://robohash.org/${this.props.contact.name}.png?size=100x100`} alt="imagine a nice robot" ></img>
                {this.props.contact.name}
            </div>
        )
    }
}

export default withRouter(ContactPreview);